import EmbedButton from './components/embedButton';
import { isSelectedBlock } from '@ali/4ever-utils';

export default function toolbar(config) {
  const embedConfig = config.embed || {};
  const embedLocaleConfig = embedConfig.locale || {};
  return {
    buttons: {
      default: EmbedButton,
    },
    locale: embedLocaleConfig,
    getTarget: (controller) => {
      if (isSelectedBlock(controller.value, 'embed')) {
        return 'embed';
      }
      return undefined;
    },
  };
}
